module.exports = "class CustomToggle extends React.Component {\n  constructor(props, context) {\n    super(props, context);\n\n    this.handleClick = this.handleClick.bind(this);\n  }\n\n  handleClick(e) {\n    e.preventDefault();\n\n    this.props.onClick(e);\n  }\n\n  render() {\n    return (\n      <a href=\"\" onClick={this.handleClick}>\n        {this.props.children}\n      </a>\n    );\n  }\n}\n\nclass CustomMenu extends React.Component {\n  constructor(props, context) {\n    super(props, context);\n\n    this.handleChange = this.handleChange.bind(this);\n\n    this.state = {\n      value: ''\n    };\n  }\n\n  handleChange(e) {\n    this.setState({ value: e.target.value });\n  }\n\n  focusNext() {\n    const input = ReactDOM.findDOMNode(this.input);\n\n    if (input) {\n      input.focus();\n    }\n  }\n\n  render() {\n    const { children } = this.props;\n    const { value } = this.state;\n\n    return (\n      <div className=\"dropdown-menu\" style={{ padding: '' }}>\n        <FormControl\n          ref={c => {\n            this.input = c;\n          }}\n          type=\"text\"\n          placeholder=\"Type to filter...\"\n          onChange={this.handleChange}\n          value={value}\n        />\n        <ul className=\"list-unstyled\">\n          {React.Children.toArray(children).filter(\n            child => !value.trim() || child.props.children.indexOf(value) !== -1\n          )}\n        </ul>\n      </div>\n    );\n  }\n}\n\nrender(\n  <Dropdown id=\"dropdown-custom-menu\">\n    <CustomToggle bsRole=\"toggle\">Custom toggle</CustomToggle>\n\n    <CustomMenu bsRole=\"menu\">\n      <MenuItem eventKey=\"1\">Red</MenuItem>\n      <MenuItem eventKey=\"2\">Blue</MenuItem>\n      <MenuItem eventKey=\"3\" active>\n        Orange\n      </MenuItem>\n      <MenuItem eventKey=\"1\">Red-Orange</MenuItem>\n    </CustomMenu>\n  </Dropdown>\n);\n"


